.card {
    min-width: 320px !important;
    min-height: 280px !important;
    margin-top: 20px !important;
}

.card-custom .card-img {
    max-width: 250px;
    max-height: 250px;
}

.bookItem {
    border: 2px solid #1e88e5;
    border-radius: 10px;
    max-width: 200px;
    min-width: 200px;
    max-height: 250px;
    min-height: 250px;
    padding: 10px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.book-item-text {
    padding-left: 5px;
    text-align: left;
    font-family: Consolas;
    font-size: 14px;
}

.book-item-img {
    max-width: 180px;
    border: 2px;
    border-radius: 10px;
}

.book-item-buttons {
    display: flex;
    justify-content: space-between;
}



